@import url(https://fonts.googleapis.com/css?family=Roboto:400,400i,700);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
nav {
    background-color: rgb(24, 110, 160);
    height: 6em;
    width: 100%;

}

nav ul {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.nav-box {
    display: flex;
    justify-content: space-between;
   
}

nav li {
    list-style: none;
    margin: 1.5em;
    color: white;
    background-color: rgba(240, 255, 255, 0.36);
    padding: 10px;
    border-radius: 5px;

}

nav a {
    text-decoration: none;
    color: white;
    font-size: 1.3em;
}

.search {
    align-self: flex-start;
    justify-self: flex-start;
}

.hamburger-icon, .cross-icon {
    display: none;
}

@media only screen and (max-width: 768px),
(min-device-width: 768px) and (max-device-width: 1024px) {


    nav {
        height: unset;


    }

    nav ul {
        justify-content: center;
        flex-direction: column;
      
    }

    .hamburger-icon {
        display: block;
        font-size: 2rem;
        color: white;
        margin: 1em;


    }

     .cross-icon {
        display: block;
        color: white;
        position: absolute;
        top: 0;
        right: 0;
        font-size: 2rem;
        margin: 1em;

    }

    .nav-items.show {
        background-color: rgb(24, 110, 160);
        height: 100vh;
        width: 100%;
        position: fixed;
    } 

    .nav-items.hide {
        display: none;
        
        
    }

    nav a {
        font-size: 1.2em;
    }

    nav li {
        margin: 1.1em;
        font-size: 0.9em;
    }


}

@media only screen and (max-width: 400px) {
    nav li {
        font-size: 0.8em;
    }
}
.search-box {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 1.2em;


}

.search-btn {
    background-color: rgba(255, 255, 255, 0.171);
    border: white 1px solid;
    border-radius: 2px;
    color: white;
    text-decoration: none;
    font-size: 1.3em;
    margin: 0 1em;
    padding: .3em;
    cursor: pointer;
}

.search {

    align-self: center;
    margin: auto;

}

.search>div {
    display: inline-block;
    position: relative;
    filter: drop-shadow(0 1px #0091c2);
    font-family: 'Roboto', 'sans-serif';
}

.search>div:after {
    content: "";
    background: white;
    width: 4px;
    height: 20px;
    position: absolute;
    top: 25px;
    right: -4px;
    transform: rotate(135deg);
}

.search>div>input {
    color: white;
    font-size: 16px;
    background: transparent;
    width: 35px;
    height: 35px;
    padding: 10px;
    border: solid 3px white;
    outline: none;
    border-radius: 35px;
    transition: width 0.5s;
}

.search>div>input::placeholder {
    color: #efefef;
    opacity: 0;
    transition: opacity 150ms ease-out;
}

.search>div>input:focus::placeholder {
    opacity: 1;
}

.search>div>input:focus,
.search>div>input:not(:placeholder-shown) {
    width: 250px;
}

@media only screen and (max-width: 400px) {
   
   .search-box {
    flex-direction: column;
    justify-content: flex-start;
    align-items: start;

   }

    .search>div>input:focus,
.search>div>input:not(:placeholder-shown) {
    width: 150px;
}


.search {
    margin: 1em;
}
.search-btn {
    border: none;
    background-color: transparent;
    margin: 0;
    padding: 0;

}


}
.background {
    background-color: #44c4e791;
    /* max-width: 1800px; */
    position: relative;
    min-height: 100vh;

}

.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 25px auto;
    max-width: 500px;
    background-color: white;
    padding: 15px;
}


.container label,
input {
    display: block;
}

form input {
    outline: none;
    margin: 0 0 20px;
    padding: 10px 15px;

    width: 300px;
    border: 1px solid #ccc;
    color: #767575;
    font-family: "Roboto";
    box-sizing: border-box;
    font-size: 14px;
    font-weight: 400;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    transition: 0.2s linear;



}

form button {
    cursor: pointer;
    background: #44c4e7;
    width: 100%;
    padding: 10px 15px;
    border: 0;
    color: #fff;
    font-family: "Roboto";
    font-size: 14px;
    font-weight: 400;
}

form button:hover {
    background: #369cb8;
}

@media screen and (max-width: 500px) {
    .container {
        width: 100%;
        max-width: 350px;
    }

    form input {
        width: 100%;
        max-width: 350px;
    }
}

@media screen and (max-width: 380px) {
    .container {
        width: 100%;
        max-width: 250px;
    }

    form input {
        width: 100%;
        max-width: 250px;
    }
}
.patient-container {
    display: flex;
    flex-direction: column;
    border-top: 1px solid black;
    align-items: center;
    min-height: 100vh;
    
}

.patient-container h2 {
    margin-top: 1em;
}

